import { get } from 'lodash/fp';

export function getPropertyMapping() {

    const propertyMapping: string[] = [
        'shop', 'pageName',
    ];

    return propertyMapping;
}

export function getPropertyValue(key: string) {

    const root: any = globalThis.ShopifyObject;

    return root ? get(root, key) : null;
}

export function getShopProperties(options?: { mapping: string[] }) {

    const propertyMapping = options?.mapping || getPropertyMapping();

    return propertyMapping.map(function (property: string, _) {
        return { [property]: getPropertyValue(property) };
    });
}