export function getPropertyMapping() {

    const propertyMapping: string[] = [
        'og:url', 'og:type', 'og:title',
        'og:price:amount', 'og:price:currency'
    ];

    return propertyMapping;
}

export function getPropertyValue(key: string, value: string) {

    const selector: string = `meta[property="${key}"]`;

    return document.querySelector(selector)?.getAttribute(value);
}

export function getPageProperties(options?: { mapping: string[] }) {

    const propertyMapping = options?.mapping || getPropertyMapping();

    return propertyMapping.map(function (property: string, _) {
        return { [property]: getPropertyValue(property, `content`) };
    });
}