import * as cookie from './cookie';
import * as meta from './meta';
import * as product from './product';
import * as shop from './shop';
import * as trekkie from './trekkie';

declare global {

    var ShopifyAnalytics: {
        meta: { [key: string]: any },
        lib: {
            trekkie: {
                defaultAttributes: {
                    shopId: number,
                    uniqToken: string,
                    visitToken: string
                }
            }
        }
    }

    var ShopifyObject: {
        pageName: string, shop: string
    }
}

export function getSummary() {

    return Object.assign({},
        cookie.getCookieProperties(),
        meta.getMetaProperties(),
        product.getPageProperties(),
        shop.getShopProperties(),
        trekkie.getTrekkieProperties(),
    );
}